.sidebar {
  background-color: #d6487e;
  border-right-color: transparent !important;
}

.sidebar .nav-link,
.sidebar .nav-link:focus:not(:hover):not(.active),
.sidebar .nav-sub ul a {
  color: #f2c5d6;
}

.sidebar .logo,
.sidebar .nav-link:hover,
.sidebar .nav-link.active,
.sidebar .nav-sub ul a:hover,
.sidebar .nav-sub ul a.active {
  color: #fff;
}

.sidebar .nav-link:hover,
.sidebar .nav-link.active {
  background-color: #db5d8d;
}

.sidebar .nav-sub ul {
  background-color: #d1336f;
}

.sidebar .nav-link.active {
  box-shadow: 3px 0 0 0 #fff inset;
}
